.personal-info-card {
  display: flex;
  justify-content: space-around;
  padding: 40px;
  .image-wrapper {
    width: 30%;
    border-radius: 10px;
    img {
      box-shadow: 0px 0px 20px #555;
      width: 100%;
      border-radius: 10px;
    }
  }
  .info {
    width: 60%;
    text-align: justify;
  }

  h3 {
    font-size: 30px;
  }
  p {
    font-size: 20px;
  }
}